import React,{Component} from 'react';
//importing compnent for moodal and button
import { Modal, Button } from 'antd';

//begins with class keyword and component and extends React.Component 
//render to addd return
class AppWorks extends Component{
    state = { visible: false };
    showModal = () => {
    this.setState({
        visible: true,
      });
    };
  
    handleOk = e => {
      console.log(e);
      this.setState({
        visible: false,
      });
    };
  
    handleCancel = e => {
      console.log(e);
      this.setState({
        visible: false,
      });
    };
    render(){
        return(
            <div id="work" className="block worksBlock">
                <div className="container-fluid">
                    <div className="titleHolder">
                        <h2>How it works</h2>
                        <p>Perspiciatis vero similique, ut ducimus modi ipsam autem tempora</p>
                    </div>
                </div>
                <div className="contentHolder">
                    <Button onClick={this.showModal}><i className="fas fa-play"></i></Button>
                </div>
                
                <Modal
                    title="Dil toda to kyun toda.."
                    visible={this.state.visible}
                    onCancel={this.handleCancel}
                    footer={null}
                    >
                    <iframe width="475" height="300" src="https://www.youtube.com/embed/VEc_PJPbjOQ" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                </Modal>
            </div>
        );
    }
}
export default AppWorks;